@import './search.scss';

.spacing-top {
  padding-top: 50px;
}

.spacing-bottom {
  padding-bottom: 50px;
}

.spacing-vertical {
  @extend .spacing-top;
  @extend .spacing-bottom;
}

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.no-drag {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

.text-uppercase {
  text-transform: uppercase;
}
