::ng-deep {
  .search-overlay {
    width: 100%;
    padding: 50px 0px 30px;
    background-image: linear-gradient(to right, #0e7dc2, #00b7a3);
    color: #fff;
    h1 {
      font-size: 31px;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 0.83em;
    }
    p {
      margin-bottom: 1em;
      margin-top: 1em;
    }
    mat-form-field {
      width: 100%;
      mat-label {
        color: white;
      }
      input {
        color: white;
        &::placeholder {
          color: white;
        }
      }
      .mat-focused .mat-form-field-label {
        color: white !important;
      }
      .mat-form-field-underline {
        background-color: white !important;
      }
      .mat-form-field-ripple {
        background-color: white !important;
      }
    }
  }
}
